html {
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  height: 100%;
  font-size: 16px;
  line-height: 1.5;
  font-family: 'PT Sans', Arial, Helvetica, sans-serif;
  color: #333333;
  margin: 0;
  background: $color-grey;
  min-width: 320px;
}

.h1 {
  font-size: 24px;
  line-height: 1.5;
  margin: 0 0 30px;
}

.h2 {
  font-size: 24px;
  line-height: 1.5;
  margin: 0 0 30px;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.line-through {
  text-decoration: line-through;
}
