.grid-flex {
    display: flex;
    flex-wrap: wrap-reverse;
    flex-direction: row-reverse;
    justify-content: flex-end;
}

.grid-table {
    width: calc(100% + 20px);
    margin: 0 -10px;
    border-spacing: 20px;
}

.grid-div-table {
    display: table;
    width: calc(100% + 20px);
    margin: 0 -10px;
    border-spacing: 20px;
}

.grid--bg {
    background-color: $color-lightgrey;
}

.grid__row {
    display: table-row;
}

.grid__item {
    background-color: $color-grey;
    margin: 10px;
    width: calc(25% - 20px);
    height: 100px;
    .grid-flex & {
        display: flex;
        justify-content: center;
        align-items: center;
        width: calc(24% - 20px);
    }
    .grid-inline-block & {
        font-size: 0;
        line-height: 0;
        display: inline-block;
        vertical-align: top;
        text-align: center;
        position: relative;
    }
    .grid-float & {
        float: left;
        position: relative;
    }
    .grid-table & {
        margin: 0;
        padding: 0;
        width: 25%;
        text-align: center;
        vertical-align: middle;
    }
    .grid-div-table & {
        display: table-cell;
        margin: 0;
        padding: 0;
        width: 25%;
        text-align: center;
        vertical-align: middle;
    }
    .grid-accordion & {
        width: auto;
        height: auto;
        margin: 10px 0;
        transition: 0.2s;
    }
}

.grid__item--pink {
    background-color: $color-pink;
    .grid-accordion &:hover {
        background-color: lighten($color-pink, 3%);
    }
}

.grid__item--green {
    background-color: $color-green;
    .grid-accordion &:hover {
        background-color: lighten($color-green, 3%);
    }
}

.grid__item--blue {
    background-color: $color-blue;
    .grid-accordion &:hover {
        background-color: lighten($color-blue, 3%);
    }
}

.grid__item--lilac {
    background-color: $color-lilac;
    .grid-accordion &:hover {
        background-color: lighten($color-lilac, 3%);
    }
}

.grid__item--yellow {
    background-color: $color-yellow;
    .grid-accordion &:hover {
        background-color: lighten($color-yellow, 3%);
    }
}

.grid__item--small {
    align-self: center;
    height: 50px;
}

.grid__num {
    color: $color-white;
    font-size: 30px;
    font-weight: bold;
    line-height: 1;
    .grid-inline-block &,
    .grid-float & {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .grid-accordion & {
        display: block;
    }
}

.grid__btn {
    border: 0;
    width: 100%;
    padding: 20px;
    margin: 0;
    box-shadow: none;
    cursor: pointer;
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
}

.grid__content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease;
}

.grid__content-txt {
    margin: 0 20px 20px;
    padding: 20px;
    font-size: 20px;
    line-height: 1.5;
    color: $color-black;
    background-color: $color-white;
}
